div,
ul {
  width: 100%;
  text-align: center;
}

.navbar-brand {
  font-size: 24px;
  font-weight: 700;
  margin-right: 0;
}

.nav-text {
  padding-top: 8px;
  font-size: 20px;
}

nav,
.navbar {
  background-color: rgb(204, 234, 255) !important;
  box-shadow: 5px 7px 14px -2px rgba(173, 173, 173, 1);
}

