@media (min-width: 787px) {
  nav,
  .navbar {
    min-height: 60px;
    flex-flow: row nowrap;
    justify-content: flex-start;
  }
}

@media (max-width: 787px) {
  nav,
  .navbar {
    min-height: 90px;
  }

  .nav-text {
    padding-top: 4px;
    font-size: 20px;
  }

  .navbar-brand {
    padding-top: 12px;
  }
}

@media (max-width: 575px) {
  nav,
  .navbar {
    min-height: 110px;
  }

  .nav-text {
    padding: 0;
    font-size: 20px;
  }

  .navbar-brand {
    padding: 0;
  }
}

main {
    margin-top: 20px;
    flex: 1 1;
}

@media (min-width: 992px) {
    .container {
        max-width: 800px;
    }
}

@media (min-width: 1500px) {
    .container {
        max-width: 1000px;
    }
}
.footer {
    flex-shrink: 0;
    width: 100%;
    height: 40px;
    padding-top: 5px;
    margin-top: 20px;
    line-height: 30px;
    background-color: rgb(204, 234, 255);
}
div {
    text-align: center;
}
img {
  height: 150px;
  width: 150px;
  margin: 10px;
  border: 5px solid rgb(204, 234, 255);
  box-shadow: 8px 11px 14px -2px rgba(173, 173, 173, 1);
}

.still {
  border: 5px solid rgb(204, 234, 255);
}

.winner {
  border: 5px solid rgb(169, 255, 183);
}

.shake {
  border: 5px solid rgb(255, 86, 86);
  -webkit-animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
          animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

@-webkit-keyframes shake {
  10%,
  90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

@keyframes shake {
  10%,
  90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

@media (min-width: 1500px) {
    img {
        height: 200px;
        width: 200px;
        margin: 10px;
      }
}

@media (max-width: 787px) {
    img {
        height: 150px;
        width: 150px;
        margin: 10px;
      }
}

